import styled from "styled-components";

const StyledButton = styled.button`
    padding: 8px 16px;
    font-size: 16px;
    border-width: 1px;
    border-radius: 8px;
    cursor: pointer;
    color: {props => props.textColor}
`;

function Button(props) {
    const {title} = props

    return (
        <StyledButton>{title}</StyledButton>
    )
};

export default Button;